var new_map
      
      var latlngDis = document.getElementById('latlngDisplay')
      var addressDis = document.getElementById('addressDisplay')
      
      function initMap(){
        geocoder = new google.maps.Geocoder()
      
        tokyo = {lat: 35.6803997, lng: 139.7690174}
      
        new_map = new google.maps.Map(document.getElementById('map'), {
          center: {lat: 35.6803997, lng: 139.7690174},
          zoom: 15,
        });
      
        marker = new google.maps.Marker({
          position: {lat: 35.6803997, lng: 139.7690174},
          map: new_map
        });
      }
      
      var geocoder
      var display = document.getElementById('display')
      
      
      function codeAddress(){
        var inputAddress = document.getElementById('address').value;
      
        google.maps.Geocoder.geocode( { 'address': inputAddress}, function(results, status) {
          if (status == 'OK') {
            map.setCenter(results[0].geometry.location);
            var marker = new google.maps.Marker({
                map: new_map,
                position: results[0].geometry.location
            });
            latlngDis.innerHTML = results[0].geometry.location;
      
            google.maps.Geocoder.geocode({ 'location': marker.getPosition()}, function(results, status) {
              if (status == "OK"){
                addressDis.innerHTML = results[0].formatted_address;
              } else {
                alert("Geocode 取得に失敗しました：" + status);
              }
            });
      
          } else {
            alert('該当する結果がありませんでした：' + status);
          }
        });   
      }
      document.getElementById("search").addEventListener("click", function() {
        codeAddress()
        });